import {recruit,recruitadd,recruitshare} from '../api/recru.js'
export default {
    head: {
        title: '蜂乐购优选招募店主、驿站站长！',
         script: [
      {
        src:
          "https://res2.wx.qq.com/open/js/jweixin-1.6.0.js"
      }
    ]
    },
    name: 'home',
    data() {
        return {
            name1:'',
            phone1:'',
            type:[],
            type_id1:'0',
            id:"0"
        }
    },
   async created() {
       const res = await recruit()
       if(res.data.code == 1) {
           this.type = res.data.data
       }
    },
   async mounted() {
        if (process.client) {
            document.title = "蜂乐购优选招募店主、驿站站长！";
        }
        var localUrl = encodeURIComponent(window.location.href.split('?')[0]);
        const {data} = await recruitshare({url:localUrl})
        wx.config({
                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: 'wx74a82c2291be2790', // 必填，公众号的唯一标识
                timestamp:data.data.timestamp, // 必填，生成签名的时间戳
                nonceStr:data.data.nonceStr, // 必填，生成签名的随机串
                signature:data.data.signature, // 必填，签名
                jsApiList: ["updateAppMessageShareData", "updateTimelineShareData"] // 必填，需要使用的JS接口列表，根据引入的微信js版本不同，jsApi名字不同，具体请看文档
        });
            wx.ready(function() {
                wx.updateTimelineShareData({     //分享到朋友圈
                    title: '蜂乐购优选招募店主、驿站站长！', // 分享标题
                    link: 'https://www.jiunidu.com/recruit', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://image.jiunidu.com/web/dianzhu_20210521.png', // 分享图标,用绝对路径
                    success: function() {
                        // 用户点击了分享后执行的回调函数
                    },
                });
                wx.updateAppMessageShareData({    //分享到微信好友
                    title: '蜂乐购优选招募店主、驿站站长！', // 分享标题
                    link: 'https://www.jiunidu.com/recruit', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://image.jiunidu.com/web/dianzhu_20210521.png', // 分享图标
                    desc:'招募店主、驿站站长、全小区给你赚钱', //分享描述
                    success: function () {
                    // 设置成功
                    }
                })
            })
            
            this.id =  this.getParams("userId")
    },
    methods:{
       async apply(id) {
                const res = await recruitadd({name:this.name1,phone:this.phone1,type:this.type_id1,share_user_id:this.id})
                if(res.data.code === 1) {
                    this.name1 = this.phone1 = ''
                    this.type_id1 = '0'
                    this.id = "0"
                    alert(res.data.msg)
                } else {
                    alert(res.data.msg)
                }
        },
         getParams(name) {
            var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
            var r = window.location.search.substr(1).match(reg);
            if (r != null) {
            return r[2];
            // decodeURIComponent(r[2])
            }
            return "0";
        }
    }   
}
